<script>
import XLSX from "xlsx";
import { UploadFileExcel } from "@/api/excelApi";
export default {
  data() {
    return {
      dataExcel: {
        FileName: "",
        DateExpired: 7,
        Columns: null,
        Rows: null,
      },
      fileName: "",
      linkExcel: "",
      dateExpired: 7,
      isShowLink: false,
      loading: false,
    };
  },
  watch: {
    dateExpired() {
      this.dataExcel.DateExpired = this.dateExpired;
    },
  },
  methods: {
    btUploadExcell() {
      this.loading = true;
      UploadFileExcel(this.dataExcel).then((res) => {
        this.loading = false;
        if (res.RespCode == 0) {
          this.isShowLink = true;
          this.linkExcel = res.UrlCode;
        } else {
          this.$notify.error({
            title: "Li",
            message: res.RespText,
          });
        }
      });
    },
    onChange(event) {
      this.file = event.target.files ? event.target.files[0] : null;
      if (this.file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          const bstr = e.target.result;
          const wb = XLSX.read(bstr, { type: "binary" });
          const wsname = wb.SheetNames[0];
          const ws = wb.Sheets[wsname];
          const data = XLSX.utils.sheet_to_json(ws, { header: 1 });
          this.convertToArrayObject(data);
          this.dataExcel.FileName = this.file.name;
        };
        this.fileName = this.file.name;
        reader.readAsBinaryString(this.file);
      }
    },
    convertToArrayObject(dataEx) {
      var data = dataEx.filter(p => p.length != 0)
      var arrayCols = data[0].map((item, index) => {
        return {
          ColIndex: index + 1,
          ColName: item,
        };
      });
      var arrayRows = [];
      for (var i = 1; i < data.length; i++) {
          for (var y = 0; y< data[i].length; y++){
            if (data[i][y] == null){
              data[i][y] = ''
            }
          }
          var row = data[i].map((item, index) => {
            // console.log(item);
            return {
              ColID: index + 1,
              RowValue: item,
              RowIndex: i,
            };
          });
          Array.prototype.push.apply(arrayRows, row);
      }
      this.dataExcel.Columns = arrayCols;
      this.dataExcel.Rows = arrayRows;
    },
  },
};
</script>

